.App {
  text-align: center;
}

h1 {
  font-family: "Bigelow Rules", cursive;
  font-size: 72px;
  /* color: #0ccac4; */
  color: white;
}

.card-list {
  width: 85vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 40px;
  margin-top: 30px;
}

.card-container {
  display: flex;
  flex-direction: column;
  background-color: #95dada;
  /* border: 1px solid grey; */
  border-radius: 15px;
  padding: 25px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  box-shadow: 0px 0px 137px -11px rgba(0, 0, 0, 0.78);
  -webkit-box-shadow: 0px 0px 137px -11px rgba(0, 0, 0, 0.78);
  -moz-box-shadow: 0px 0px 137px -11px rgba(0, 0, 0, 0.78);
}

.card-container:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.search-container {
  margin-top: 15px;
  margin-bottom: 30px;
}
.search-label {
  margin-top: 15px;
  font-size: 16px;
  margin-bottom: 10px;
  margin-right: 10px;
}
.search-input {
  -webkit-appearance: none;
  border: none;
  margin-bottom: 15px;
  outline: none;
  padding: 10px;
  width: 200px;
  line-height: 30px;
  border-radius: 10px;
  background-color: rgb(228, 228, 228);
}

